.menu {
  display: flex;
  justify-content: space-around;
  max-width: 1200px;
  margin: 0 auto;
  align-items: center;
  border: 1px solid grey;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}
.menu__title {
  flex-grow: 2;
}
.menu__cart {
  margin-left: 20px;
  position: relative;
}

.menu__title,
.menu__totalPrice,
.menu__cart {
  padding: 10px;
}
.menu__totalPrice,
.menu__cart {
  border-left: 1px solid grey;
}
.menu__cart {
  cursor: pointer;
}

.menu__cart:hover {
  background-color: rgba(0, 0, 0, 0.1);
}
.content {
  max-width: 1200px;
  margin: 0 auto;
}
.products {
  display: flex;
  flex-wrap: wrap;
  max-width: 970px;

  justify-content: space-between;
}
.product__item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-basis: 230px;
  border: 1px solid grey;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -ms-border-radius: 3px;
  -o-border-radius: 3px;
  margin-bottom: 20px;
}

.product__image img {
  width: 100%;
  height: 300px;
}

.product__title {
  font-weight: bold;
  font-size: 18px;
}
.product__content {
  padding: 10px;
}
.product__author {
  color: grey;
}
.product__content {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.product__btn {
  color: white;
  width: 100%;
  background-color: #21ba45;
  border: none;
  padding: 10px;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -ms-border-radius: 3px;
  -o-border-radius: 3px;
  cursor: pointer;
}

.product__price {
  color: grey;
  margin-top: 20px;
}
.content {
  margin-top: 20px;
  display: flex;
  align-items: flex-start;
}

.filter {
  border: 1px solid grey;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  margin-left: 20px;
}

.filter__type {
  border-bottom: 1px solid grey;
  cursor: pointer;
  padding: 10px;
}
.filter__type:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

.filter__type:last-child {
  border: none;
}
.filter__search {
  padding: 10px;
}
.cart {
  display: block;
  margin: 0 auto;
  max-width: 600px;
  padding: 0px 10px;

  border-radius: 5px;

  background-color: white;
  border: 1px solid grey;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}
.cart__item {
  display: flex;
  height: 70px;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.cart__img img {
  height: 30px;
  width: 30px;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
}
.cart__book-title {
  margin-right: auto;
  margin-left: 5px;
}
.cart__delete {
  color: white;
  font-weight: bold;
  background-color: #db2828;
  padding: 10px;
  border: none;
  cursor: pointer;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -ms-border-radius: 3px;
  -o-border-radius: 3px;
}

@media (max-width: 992px) {
  .cart {
    left: -205%;
  }
}

@media (max-width: 768px) {
  .content {
    flex-direction: column;
  }
  .filter {
    order: 1;

    padding: 0;
    height: auto;
    width: 170px;
    margin: 0 auto;
    margin-bottom: 10px;
  }

  .products {
    order: 2;
  }
}

@media (max-width: 500px) {
  .products {
    justify-content: center;
  }
  .cart {
    left: -120%;
    width: 150px;
  }

  .cart__item {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: auto;
    padding: 10px 0px;
  }
}
